
.field.is-horizontal {
    .field-label {
        user-select: none;
    }
    .field-label.is-normal {
        min-width: 160px;
    }
}

.field.is-mandatory {
    label.label::after {
        color: red;
        content: ' *';
    }
}

//FileInput
.dropzone {
    border: 1px dashed #ccc;
    padding: 20px; 
    .fa.fa-file-upload {
        padding: auto;
        font-size: 100px;
    }
}
